.fav-movie {
    margin-bottom: 15px;
    figure {
        position: relative;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        img {
            height: 400px ;
            display: block;
            margin: auto;
        }
        
        figcaption {
            text-align:center;

        }
        button.btn {
            display: block;
            margin: auto;
        }
    }

}

.card {
    margin-bottom: 15px ;
}